const personas = {
  1: {
    name: "George P. Burdell",
    spec: "cpr",
    age: 30,
    completed: []
  },
  2: {
    name: "Alan Turing",
    spec: "cs",
    age: 20,
    completed: [6250, 6035, 6200]
  },
  3: { name: "Ada Lovelace", spec: "ii", age: 33, completed: [6515, 6601] },
  4: { name: "Charles Babbage", spec: "ml", age: 40, completed: [7646] }
  // 5: { name: "Steve Jobs", spec: "na", age: 25, completed: [] }
};

const dynData = (data, persona) => {
  return data.map(r => {
    const isAvailable = Math.random() < 0.5 ? true : false;
    const mockRem = isAvailable
      ? Math.floor(Math.random() * (r.Cap - 1) + 0)
      : 0;
    const mockWRem =
      r["WL Cap"] > 0 ? Math.floor(Math.random() * (r["WL Cap"] - 1) + 0) : 0;

    return {
      ...r,
      priority: persona
        ? specs[persona.spec].req.includes(r.Crse)
          ? "R"
          : "E"
        : "",
      Select: isAvailable
        ? "Register"
        : mockWRem > 0
        ? "Waitlist"
        : "Class Full",
      Difficulty: Math.ceil(Math.random() * 10),
      Workload: Math.ceil(Math.random() * 10),
      Rem: mockRem,
      "WL Rem": mockWRem
    };
  });
};

